import React, {Component} from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';


class Table extends Component {
  constructor(props) {
    super(props)
    this.state = {
      users: [],
      isLoading: false,
      isError: false
    }
  }



async componentDidMount() {
  this.setState({ isLoading: true })
  const response = await fetch('http://127.0.0.1:8000/api/table/')
  if (response.ok) {
    const tabledate = await response.json()
    console.log(tabledate)
    this.setState({ tabledate, isLoading: false })
  } else {
    this.setState({ isError: true, isLoading: false })
  }
  console.log(Object.keys(this.state.tabledate))
}

renderTableHeader = () => {
  return Object.keys(this.state.tabledate["results"]).map(attr => <th key={attr}>
    {attr.toUpperCase()}
  </th>)
}

renderTableRows = () => {
  return this.state.tabledate.map( field => {
    return (
      <tr key={field.id}>
        <td>{field.id}</td>
        <td>{field.date}</td>
      </tr>
    )
  }
  )

}


render(){
  const {tabledate, isLoading, isError} = this.state

  if(isLoading){
    return <div>Loading . . .</div>
  }

  if(isError){
    return <div>Error . . .</div>
  }
  return tabledate.length > 0
  ? (
    <table>
      <thead>
        <tr>
          {this.renderTableHeader()}
        </tr>
      </thead>
      <tbody>
        {this.renderTableRows()}
      </tbody>
    </table>

  ):(
    <div>No components </div>
  )
  }
}



export default Table



